<template>
  <div>
    <!-- 搜索与添加区域 -->
    <el-row :gutter="20">
      <el-col :span="8">
        <el-input placeholder="请输入内容" v-model="input1" @blur="saveHistoryData">
          <!-- <el-select v-model="select" slot="prepend" placeholder="请选择">
          <el-option label="餐厅名" value="1"></el-option>
          <el-option label="订单号" value="2"></el-option>
          <el-option label="用户电话" value="3"></el-option>
          </el-select>-->
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </el-col>
      <el-col :span="4">
        <el-button type="primary">添加用户</el-button>
      </el-col>
    </el-row>

    <!-- 用户列表区域 -->
    <el-table :data="userList" stripe border>
        <el-table-column prop="username" label="用户名">
        </el-table-column>
        <el-table-column prop="password" label="密码">
        </el-table-column>
        <el-table-column prop="name" label="姓名">
        </el-table-column>
      </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
        input1: '',
        pageInfo: {
            page: 1,
            size: 5,
            total: 0
        },
        userList: []

    };
  },
  created() {
    this.setHistoryData();
    this.getUserlist();
    // console.log(this.$route.path)
  },
  methods: {
    // 动态渲染做出导航菜单
    async getUserlist() {
      let tokenstr = window.localStorage.getItem("token");
      const res = await this.$http.get("api/getuserlist", {
        params: this.pageInfo
      });
    //   console.log(res);
      if(res.code != 200) {
          this.$message.error('获取用户列表失败');
      } else {
          this.userList = res.data.object;
          this.pageInfo.total = res.data.total;
      }
    // console.log(this.userList);
    // console.log(this.pageInfo);
    //   this.menuList = res.data;
    },
    // 保存已输入数据
    saveHistoryData() {
        console.log('savein')
        // 把已输入数据保存到sessionStorage里面
        window.sessionStorage.setItem("userinput1", this.input1);
    },
    // 填入历史数据
    setHistoryData() {
        console.log(window.sessionStorage.getItem('userinput1'))
        if( window.sessionStorage.getItem('userinput1')) {
            this.input1 = window.sessionStorage.getItem('userinput1');
        }
    }
  }
};
</script>

<style >
.el-table {
    margin-top: 12px;
    font-size: 12px;
}
</style>